.copyright {
  display: flex;
  align-items: center;
  justify-content: center;

  @include ptmono(13px, 24px);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: rgba($dark-blue, 0.5);

  a {
    color: inherit;
    text-decoration: none;
  }
}

.copyright__wrapper {
  width: 1440px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  padding: 14px 25px 13px;
}

.copyright__rights {
  margin-right: 75px;
}

.copyright__term {
  margin-right: 10%;
}

.copyright__dev {
  margin-left: auto;
}

@media (max-width: $desktop-width - 1) {
  .copyright__wrapper {
    padding-top: 19px;
    padding-bottom: 19px;
    justify-content: space-between;
  }

  .copyright__rights,
  .copyright__term {
    display: none;
  }

  .copyright__dev {
    margin-left: 0;
  }
}

@media (max-width: $tablet-width - 1) {
  .copyright__wrapper {
    padding-top: 17px;
    padding-bottom: 15px;
    flex-direction: column;
    text-align: center;

    span {
      padding: 11px 0;
    }
  }
}
